        this.$refs[name].validate((valid) => {
          if (valid) {
            this.saveLoading=true;
            this.saveOrUpdateGoodsInfo(this.addForm.name,this.addForm.code,this.addForm.classify,this.addForm.brand,this.addForm.model,this.addForm.unit,this.addForm.maxInventory,this.addForm.minInventory,this.editId);
          }
