      //åè¡¨
      getGoodsFilesList(pageNum,pageSize,name) {
        this.data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getGoodsInfoPage.action',
          data:{
            pageNum:pageNum,
            pageSize:pageSize,
            name:name
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData && aData.list.length>0){
              aData.list.map((item)=>{
                this.data.push({
                  id:item.id,
                  name:item.name,
                  code:item.code,
                  classify:item.goodsKind,
                  brand:item.brand,
                  model:item.size,
                  unit:item.unit,
                  maxInventory:item.maxInventory,
                  minInventory:item.minInventory
                })
              })
            }
            this.total = aData.totalCount;
            this.selectLoading = false;
          })
          .catch((error) => {
            this.selectLoading = false;
            this.postErrorTip('è¯·æ±å¤±è´¥');
            console.log(error);
