            this.addForm.name = aData.name;
            this.addForm.code = aData.code;
            this.oldCode = aData.code;
            this.addForm.classify = aData.goodsKindId;
            this.addForm.brand = aData.brand;
            this.addForm.model = aData.size;
            this.addForm.unit = aData.unit;
            this.addForm.maxInventory = aData.maxInventory;
            this.addForm.minInventory = aData.minInventory;
          })
          .catch((error) => {
