            }
            this.saveLoading = true;
            this.getGoodsFilesList(this.pageNum,this.pageSize,this.goodsName);
          })
          .catch((error) => {
            this.saveLoading = true;
            this.postErrorTip('è¯·æ±å¤±è´¥');
            console.log(error);
          });
      },
      confirmDelHandle(){
        this.delModal = false;
        this.saveLoading = false;
        this.deleteGoodsInfo(this.editId);
      },
      //å¯¼å¥
      importHandle(){
        this.importErrorMsg = [];
        this.importModal = true;
        this.$refs['importForm'].resetFields();
        this.importForm.file = null;
      },
      handleUpload (file) {
        if(!utils.regImportType.test(file.name)){
          this.postWarningTip(utils.regImportTypeMsg);
        }else{
          this.importForm.file = file;
          this.importForm.fileName = file.name;
          this.$refs['importForm'].validateField('fileName');
        }
        return false;
      },
      importSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.saveLoading=true;
            this.validateImportGoodsInfo();
          }
        })
      },
      saveImport() {
        let formData = new FormData();
        formData.append('upload',this.importForm.file);
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!doImportGoodsInfo.action',
          data:formData,
          processData: false,
          contentType: false,
          transformRequest: [function (data) {
            return data;
          }],
        })
          .then((response) => {
            this.saveLoading=false;
            this.importModal=false;
            this.importForm.file = null;
            this.getGoodsFilesList(this.pageNum,this.pageSize,this.goodsName);
          })
          .catch((error) => {
            this.saveLoading=false;
            this.importModal=false;
            this.postErrorTip('è¯·æ±å¤±è´¥');
            this.$refs.upload.value = '';
            console.log(error);
          });
      },
      //å¯¼å¥åéªè¯
      validateImportGoodsInfo() {
        let formData = new FormData();
        formData.append('upload',this.importForm.file);
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!validateImportGoodsInfo.action',
          data:formData,
          processData: false,
          contentType: false,
          transformRequest: [function (data) {
            return data;
          }],
        })
          .then((response) => {
            const aData = response.data;
            this.importErrorMsg = aData;
            if(aData.length<=0){
              this.saveImport();
            }else{
              this.saveLoading=false;
            }
          })
          .catch((error) => {
            this.saveLoading=false;
            this.postErrorTip('è¯·æ±å¤±è´¥');
            console.log(error);
          });
      },

      //å¯¼åº
      exportHandle(){
        location.href = this.serverPath+"/sr/storeroomData!exportGoodsInfo.action?ids="+this.allId.join(',');
      },
      selectAllHandle(selection){
        this.allId = selection.map((item)=>{
          return item.id;
        });
      },
      //=======ææªä½¿ç¨==========
      handleCheckAll() {
        if (this.indeterminate) {
          this.checkAll = false;
        } else {
          this.checkAll = !this.checkAll;
        }
        this.indeterminate = false;

        if (this.checkAll) {
          this.checkAllGroup = ['1','2','3'];
        } else {
          this.checkAllGroup = [];
        }
      },
      checkAllGroupChange (data) {
        if (data.length === 3) {
          this.indeterminate = false;
          this.checkAll = true;
        } else if (data.length > 0) {
          this.indeterminate = true;
          this.checkAll = false;
        } else {
          this.indeterminate = false;
          this.checkAll = false;
        }
      },
      //=================
      //å¯¼åºäºç»´ç 
      exportQR(){
        location.href = this.serverPath+"/sr/storeroomData!exportGoodsInfoQRCode.action?ids="+this.allId.join(',');
      }
    },
    mounted() {

    }
  }
</script>
<style lang="less" scoped>
  .ivu-table .demo-table-info-row td{
    background-color: rgba(28, 144, 243, 0.1);
  }
  .zd-cm-gf-detail{
    width: 500px;
  }
  .zd-cm-gf-detail div{
    clear: both;
  }
  .zd-cm-gf-detail p{
    width: 50%;
    margin:10px 0 15px;
    float: left;
  }
  .zd-cm-gf-detail p span:first-child{
    width: 100px;
    text-align: right;
    display: inline-block;
    float: left;
  }
  .zd-cm-gf-detail p span:last-child{
    width: 150px;
    display: inline-block;
    float: left;
  }
</style>



